@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.html, body {
  background-color: #ECBE70;
  left: 0 !important;
  width: 100% !important;
}

.App {
  text-align: center;
  min-height: 70vh;
  left: 0 !important;
  width: 100% !important;
  margin: 0px;
  padding: 0px;
}


.container {
    background-color: #ECBE70;
    left: 0 !important;
    width: 100% !important;
    margin: 0px !important;
    padding: 0px !important;
    max-width: 100% !important;
}

div.imageContainer {
    position: relative;
    text-align: center;
    margin-bottom: 0px;
    padding-bottom: 0px;
}

img.leadspace {
    width: 100%;
    height: 100vh;
    position: relative;
}

h1.header-title {
    font-family: "Roboto Mono", monospace;
    position: absolute;
    top: 45%;
    left: 50%;
    font-size: 40px;
    transform: translate(-50%, -50%);
    margin: 0px;
}

p.header {
    font-family: "Roboto Mono", monospace;
    position: absolute;
    top: 52%;
    left: 50%;
    font-size: 20px;
    transform: translate(-50%, -50%);
}

div.card {
    border-radius: 5px;
    background-color: #003057;
    opacity: 0.8;
    width: 350px;
    height: 300px;
}

img.card-img {
    height: 100%;
    width: 50%;
    float: left;
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
}

.card-title.name {
    margin-bottom: 5px;
    font-weight: 800;
}

.card-title.major {
    margin-bottom: 0px;
    font-size: 16px;
}

.card-title.year {
    font-size: 16px;
    margin-bottom: 30px;
}

.cardContainer {
    background-image: url(/advice-to-first-years/static/media/wallpaper.39645db9.png);
    padding: 50px;
}

a {
    margin-top: auto;
}

i.arrow.right.icon {
    margin-right: 0px;
    margin-left: 6px;
}

.card-body {
    font-family: "Roboto Mono", monospace;
    color: white;
    padding: 20px 10px 10px 10px;
    width: 50%;
    float: right;
    height: 100%;
}

a.align-self-end:hover {
    text-decoration: underline !important;
    color: white;

}

a.align-self-end {
    color: white;
    text-decoration: none;
}
div.page {
    background-image: url(/advice-to-first-years/static/media/wallpaper.39645db9.png);
    left: 0 !important;
    width: 100% !important;
    margin: 0px !important;
    padding: 0px !important;
    max-width: 100% !important;
    height: 100%;
}

div.navBar {
    height: 50px;
}

img.logo {
    width: 15%;
    float: left;
    margin-left: 15px;
    margin-top: 5px;
}

div.row {
    background-color: #e5e5e5;
    margin: 15px 50px 50px 50px;
    border-radius: 10px;
}

div.col {
    padding-top: 40px;
    padding-left: 50px;
    padding-right: 40px;
    padding-bottom: 40px;
    text-align: left;
}

a.welcome {
    font-family: "Roboto Mono", monospace;
    color: black;
    float: right; 
    font-size: 15px;
    margin-right: 25px; 
    margin-top: 20px;
}

h3.major {
    margin-top: 10px;
    margin-bottom: 0px;
}

h3.year {
    margin-top: 5px;
    margin-bottom: 0px;
}

h3.job {
    margin-top: 5px;
}

img.profilepicture {
    margin-top: 20px;
}
